import { instituteStudentApi } from "./instituteStudentApi";
import {
  instituteStudentAddSectionFunction,
  instituteStudentFormDetailFunction,
  instituteStudentRearrangeSectionFunction,
  instituteStudentSectionAddChecklistFunction,
  instituteStudentUpdateSectionChecklistFunction,
} from "./institute_student_query_mutation_function";

const instituteStudentApiDynamic = instituteStudentApi.injectEndpoints({
  endpoints: (builder) => ({
    instituteStudentAddSection: instituteStudentAddSectionFunction(builder),
    instituteStudentSectionAddChecklist:
      instituteStudentSectionAddChecklistFunction(builder),
    instituteStudentRearrangeSection:
      instituteStudentRearrangeSectionFunction(builder),
    instituteStudentUpdateSectionChecklist:
      instituteStudentUpdateSectionChecklistFunction(builder),
    instituteStudentFormDetail: instituteStudentFormDetailFunction(builder),
  }),
});

export const {
  useInstituteStudentAddSectionMutation,
  useInstituteStudentSectionAddChecklistMutation,
  useInstituteStudentRearrangeSectionMutation,
  useInstituteStudentUpdateSectionChecklistMutation,
  useInstituteStudentFormDetailQuery,
} = instituteStudentApiDynamic;
