import {
  useInstituteStudentAddSectionMutation,
  useInstituteStudentFormDetailQuery,
  useInstituteStudentRearrangeSectionMutation,
  useInstituteStudentSectionAddChecklistMutation,
  useInstituteStudentUpdateSectionChecklistMutation,
} from "../../../Services/InstituteStudent/instituteStudentApiDynamic";

export const useInstituteStudentAddSection = () => {
  const [instituteStudentAddSection] = useInstituteStudentAddSectionMutation();
  return [instituteStudentAddSection];
};
export const useInstituteStudentSectionAddChecklist = () => {
  const [instituteStudentSectionAddChecklist] =
    useInstituteStudentSectionAddChecklistMutation();
  return [instituteStudentSectionAddChecklist];
};
export const useInstituteStudentRearrangeSection = () => {
  const [instituteStudentRearrangeSection] =
    useInstituteStudentRearrangeSectionMutation();
  return [instituteStudentRearrangeSection];
};
export const useInstituteStudentUpdateSectionChecklist = () => {
  const [instituteStudentUpdateSectionChecklist] =
    useInstituteStudentUpdateSectionChecklistMutation();
  return [instituteStudentUpdateSectionChecklist];
};

export const useInstituteStudentFormDetail = (args) => {
  const { data, refetch, isFetching } = useInstituteStudentFormDetailQuery(
    args?.data,
    {
      skip: args?.skip,
    }
  );
  return {
    instituteStudentFormDetail: data,
    instituteStudentFormDetailRefetch: refetch,
    instituteStudentFormDetailLoading: isFetching,
  };
};
