import { PATCH, POST } from "../UrlConfig/MethodType";

export const instituteStudentAddSectionFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/ins/new/form/${data.formId}/section/student/query`,
      method: POST,
      body: data.bodyParameter,
    }),
  });
};

